<template>
  <div class="invitation">
    <van-image class="img"
               fit="cover"
               :src="require('@/assets/invitationBg.png')" />
    <div class="main">
      <div class="title">邀请好友</div>
      <div class="tips">我们一起创造财富</div>
      <div class="box">
        <div class="tip1 tip">方法一：使用钱包，扫描二维码</div>
        <div class="qrCode"
             id="qrCode">
          <vue-qr :text="url"
                  class="download"
                  id="download"
                  :correctLevel="3"
                  :margin="10"
                  :logoSrc="require('@/assets/head_Star.png')"
                  :logoMargin="5"></vue-qr>
        </div>
        <div class="save"
