        <div class="tip2 tip">方法二：复制链接，在浏览器中打开</div>
        <van-button class="btns"
                    type="info"
                    block
                    color="#096CFF"
                    @click="copy(url)">复制邀请链接</van-button>

      </div>
    </div>
  </div>
</template>

