      tpIsConnected: false
    }
  },
  mounted () {
    // tp钱包
    let tp = require('tp-js-sdk')
    this.tpIsConnected = tp.isConnected()
  },
  methods: {
    download () {
      if (this.tpIsConnected) {
        tp.saveImage({
          url: this.url
        });
      } else {
        // var base64 = document.getElementById('download').src;
        // var arr = base64.split(',');
        // var bytes = atob(arr[1]);
        // let ab = new ArrayBuffer(bytes.length);
        // let ia = new Uint8Array(ab);
        // for (let i = 0; i < bytes.length; i++) {
        //   ia[i] = bytes.charCodeAt(i);
        // }
        // var blob = new Blob([ab], { type: 'application/octet-stream' });
        // var url = URL.createObjectURL(blob);
        // var a = document.createElement('a');
        // a.href = url;
        // a.download = new Date().valueOf() + ".png";
        // var e = document.createEvent('MouseEvents');
        // e.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
        // a.dispatchEvent(e);
        // URL.revokeObjectURL(url);
      }
    },
    // 复制
    copy (val) {
      let domInput = document.createElement('input');
      domInput.value = val;
      document.body.appendChild(domInput);
      domInput.select();
      document.execCommand("Copy");
      Toast(this.$t('copySuccess'))
      domInput.remove()
    }
  }
}
</script>
<style lang="less" scoped>
.invitation {
  padding-bottom: 55px;
  .img {
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }
  .main {
    position: relative;
    z-index: 1;
    .box {
      width: 336px;
      height: 406px;
      margin: auto;
      background-image: url(../../assets/invitationCardBg.png);
      background-size: 100%;
      background-repeat: no-repeat;
      padding: 0 30px;
      box-sizing: border-box;
      .tip {
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        color: #2a2d37;
      }
      .tip1 {
        margin-bottom: 10px;
        padding-top: 35px;
      }
      .qrCode {
        margin: auto;
        width: 172px;
