        height: 50px;
        border-radius: 3px;
      }
    }
    .title {
      padding-top: 20px;
      margin-bottom: 15px;
      text-align: center;
      font-size: 45px;
      font-weight: 800;
      color: #fefefe;
      background: linear-gradient(
        0deg,
